<template>
  <div class="app">
    <!--Navbar-->
    <MDBNavbar dark bg="dark" container>
      <router-link to="/">
        <MDBNavbarBrand>
          <img
            src="./assets/logonavbar.png"
            height="30"
            alt=""
            loading="lazy"
          />
        </MDBNavbarBrand>
        <!--NavbarDD-->
      </router-link>
      <MDBDropdown v-model="pageDD">
        <MDBDropdownToggle @click="pageDD = !pageDD" color="light">
          Pages
        </MDBDropdownToggle>
        <MDBDropdownMenu>
          <router-link to="/"
            ><MDBDropdownItem tag="button">Home</MDBDropdownItem></router-link
          >
          <router-link to="/อะเบ้า"
            ><MDBDropdownItem tag="button">About</MDBDropdownItem></router-link
          >
          <router-link to="/ไอเบสมันโม้"
            ><MDBDropdownItem tag="button">Roll</MDBDropdownItem></router-link
          >
        </MDBDropdownMenu>
      </MDBDropdown>
      <!--NavbarDD End-->
      <div clasee="clock">{{ dateTime.hours }}:{{ dateTime.minutes }}:{{ dateTime.seconds }}</div>
    </MDBNavbar>
    <!--Navbar End-->

    <div class="page">
      <router-view />
      <index></index>
    </div>
  </div>
</template>

<script>
import index from './components/index.vue'
import { MDBNavbar, MDBNavbarBrand, MDBDropdown, MDBDropdownToggle, MDBDropdownMenu, MDBDropdownItem } from 'mdb-vue-ui-kit';
import { ref } from 'vue';
const date = new Date();

export default {
  name: 'App',
  components: {
    MDBNavbar,
    MDBNavbarBrand,
    MDBDropdown,
    MDBDropdownToggle,
    MDBDropdownMenu,
    MDBDropdownItem,
    index
  },
  data() {
    return {
      dateTime: {
        hours: date.getHours(),
        minutes: date.getMinutes(),
        seconds: date.getSeconds(),
      },
      timer: undefined,
    };
  },
  methods: {
    setDateTime() {
      const date = new Date();
      this.dateTime = {
        hours: date.getHours(),
        minutes: date.getMinutes(),
        seconds: date.getSeconds(),
      };
    },
  },
  beforeMount() {
    this.timer = setInterval(this.setDateTime, 1000);
  },
  beforeUnmount() {
    clearInterval(this.timer);
  },
  setup() {
    const pageDD = ref(false);

    return {
      pageDD
    }
  },
};
</script>

<style>
@import url('https://fonts.googleapis.com/css2?family=Maven+Pro&family=Prompt:wght@300&display=swap');
#app {
  font-family: 'Maven Pro', 'Prompt', sans-serif;
  text-emphasis-color: #ffffff;
}
</style>
